@media (max-width: 550px) {
    .big-container {
        display: none;
    }
 }
 @media (min-width: 551px) {
    .small-container {
        display: none;
    }
 }

 .responsive-iframe-container {
   position: relative;
   padding-bottom: 70vh;
   padding-top: 30px;
   height: 0;
   overflow: hidden;
}
.responsive-iframe-container iframe {
   position: absolute;
   top: 0%;
   left: 5%;
   width: 90%;
   height: 90%;

}

.title {
   font-size: calc(5px + 2vmin);
   font-weight: bold;
   color: white;
   text-align: center;
   margin-top: 5%;
}

.description {
   font-size: calc(5px + 2vmin);
   color: white;
   text-align: center;
}

.Calendar {
   height: 100vh;
   width: 100%;
   background-color: #042C83;
}