.player {
    padding: 5%;
    margin-right: auto;
    margin-left: auto;
    /* height:75vw; */
}

@media (max-width: 350px) {
    .large-container {
        display: none;
    }
    .medium-container {
        display: none;
    }
}

@media (max-width: 700px) {
    .large-container {
        display: none;
    }
    .tiny-container {
        display: none;
    }
}
@media (min-width: 701px) {
    .tiny-container {
        display: none;
    }
    .medium-container {
        display: none;
    }
}